300px}.food-list{left:30%;top:100px;min-height:250px}.clothes-list{left:30%;top:150px;min-height:200px}.clean-list{left:30%;top:200px;min-height:150px}.tools-list{left:30%;top:250px;min-height:100px}.other-list{left:30%;right:0;top:300px;min-height:100px}.button{position:absolute;left:0;text-transform:uppercase;width:30%;height:50px;cursor:pointer;border:1px solid gray;border-bottom:0;border-top-right-radius:10px;border-top-left-radius:10px}.all{top:0}.food{top:50px}.clothes{top:100px}.clean{top:150px}.tools{top:200px}.other{top:250px}.conteiner-form{width:100%;margin-top:60px;height:250px;padding:2px}@media (min-width: 800px){.conteiner-form{height:300px;margin-bottom:20px}}.hide{display:none}.form{display:flex;flex-direction:column;height:35vh}.form .form_input{display:flex;flex-basis:25%;justify-content:center;align-items:center}.form .form_important{display:flex;flex-basis:15%;font-size:20px;justify-content:center;align-items:center}.form .form_kind{display:flex;flex-basis:25%;justify-content:center;align-items:center}.form .form_button-area{display:flex;flex-basis:25%;justify-content:center;align-items:center}.listli{display:flex;width:100%;cursor:pointer}.listli:nth-child(even){background:#ccc}.list_item{font-size:18px;width:100%;padding:5px;text-align:left}.list_basket{font-size:20px;padding:5px 5px}@media (min-width: 1200px){.list_basket{padding-left:30px}}.important{color:#b11adc}.list_delete-icon{font-size:20px;padding:5px 10px;cursor:pointer}@media (min-width: 1200px){.list_delete-icon{padding-right:20px}}.bought{color:gray;text-decoration:line-through}

